import { Action } from '@ali/4ever-cangjie';
import { ITableSelection } from '../types';
import Table from '../../mo/models';

export const TABLE_SET_BORDER = Symbol('TABLE_SET_BORDER');

export function setTableBorder(table, selection, borderSetType, color, sz, type) {
  return {
    type: TABLE_SET_BORDER,
    payload: {
      node: table,
      selection,
      borderSetType,
      color,
      sz,
      type,
    },
  };
}

interface TableSetTableBorderPayload {
  node: Table;
  selection: ITableSelection;
  borderSetType: any;
  color: string;
  sz: number;
  type: string;
}

interface TableSetTableBorderAction {
  type: typeof TABLE_SET_BORDER;
  payload: TableSetTableBorderPayload;
}

export function isTableSetTableBorderAction(
  action: Action,
): action is TableSetTableBorderAction {
  return action.type === TABLE_SET_BORDER;
}
